import Swal from 'sweetalert2';

const messages = {
  loginText: '',
  loginErrorText: '',
  checkSuccessText: '',
  checkErrorText: '',
  addText: '',
  editText: '',
  deleteText: '',
};
//  Toast 
const Toast = Swal.mixin({
  toast: true,
  position: 'top',
  showConfirmButton: false,
  timer: 1500,
  timerProgressBar: true,
  didOpen: (toast) => {
    toast.addEventListener('mouseenter', Swal.stopTimer);
    toast.addEventListener('mouseleave', Swal.resumeTimer);
  },
});

//  success message
export function loginMsg() {
  Toast.fire({
    icon: 'success',
    title: `${messages.loginText}`,
  });
}
//  error message
export function loginErrorMsg() {
  Toast.fire({
    icon: 'error',
    title: `${messages.loginErrorText}`,
  });
}

//  success message
export function checkSuccessMsg() {
  Toast.fire({
    icon: 'success',
    title: `${messages.checkSuccessText}`,
  });
}

//  success message
export function checkErrorMsg() {
  Toast.fire({
    icon: 'error',
    title: `${messages.checkErrorText}`,
  });
}

//  success message
export function addMsg() {
  Toast.fire({
    icon: 'success',
    title: `${messages.addText}`,
  });
}
//  success message
export function editMsg() {
  Toast.fire({
    icon: 'success',
    title: `${messages.editText}`,
  });
}
//  success message
export function deleteMsg() {
  Toast.fire({
    icon: 'success',
    title: `${messages.deleteText}`,
  });
}
//  message
export function customizeSuccessMsg(msg) {
  Toast.fire({
    icon: 'success',
    title: msg,
  });
}
//  message
export function customizeErrorMsg(msg) {
  Toast.fire({
    icon: 'error',
    title: msg,
  });
}

//  Toast2 
const Toast2 = Swal.mixin({
  toast: true,
  position: 'center',
  showConfirmButton: false,
  timer: 1000,
  timerProgressBar: true,
  didOpen: (toast) => {
    toast.addEventListener('mouseenter', Swal.stopTimer);
    toast.addEventListener('mouseleave', Swal.resumeTimer);
  },
});

export function searchNoKeyword() {
  Toast2.fire({
    icon: 'warning',
    title: '',
  });
}

export function searchDataNum(num) {
  Toast2.fire({
    icon: 'success',
    title: ` ${num} `,
  });
}
